import { isNaN, toNumber } from 'lodash';
import { PRECISION } from '../common/constant';
import { getLang } from '../common/i18n';

const FORMATTERS = {
  en_US: ['KMBTP'.split(''), [1e3, 1e3, 1e3, 1e3, 1e3]],
  zh_CN: [
    ['', ''],
    [10000, 1e4],
  ],
};

/**
 * 
 * 
 *  11w 3,459
 *  21w 3.23.4 2,234
 * @param v
 * @param fixed
 * @param formatter
 */
export const auto = (
  v: number,
  fixed = 2,
  formatter = FORMATTERS[getLang()] || FORMATTERS.zh_CN,
): string => {
  if (typeof v !== 'number' || isNaN(v)) {
    return '';
  }
  // let n = Math.abs(v); // abs
  let n = v;
  // 
  const [texts, powers] = formatter;

  let loop = 0;
  let power;
  let running = true;

  while (running) {
    power = powers[loop] as number;

    if (n >= power && loop < texts.length) {
      n /= power;
    } else {
      running = false;
    }
    loop += 1;
  }

  // parseFloat  toFixed  0 
  // 123.toFixed(2) = '123.00' '123'
  n = parseFloat(n.toFixed(fixed));

  // 
  const output = n >= 1000 ? n.toLocaleString('en') : `${n}`;

  // 
  return loop === 0 ? output : `${output} ${texts[loop - 1]}`;
};

// Spreadsheet
// https://juejin.im/post/5ce373d651882532e409ea96
export const parseNumberWithPrecision = (value: number | string) => {
  return Number.parseFloat((toNumber(value) || 0).toPrecision(PRECISION));
};
