import { EMPTY_FIELD_VALUE } from '../../common/constant';
import type { Node } from '../../facet/layout/node';

export function getDimsCondition(parent: Node, force?: boolean) {
  const cond = {};
  let p = parent;
  while (p && p.key) {
    /**
     * 
     * force
     */
    if ((!p.isTotalRoot || force) && p.value !== EMPTY_FIELD_VALUE) {
      cond[p.key] = p.value;
    }
    p = p.parent;
  }
  return cond;
}
