/**
 * @description:  svg  viewBox svg  '0 0 1024 1024'
 */
import { Group, Shape, type ShapeAttrs } from '@antv/g-canvas';
import { omit, clone } from 'lodash';
import { DebuggerUtil, type S2CellType } from '..';
import { getIcon } from './factory';

const STYLE_PLACEHOLDER = '<svg';

const SVG_CONTENT_TYPE = 'data:image/svg+xml';

// Image 
const ImageCache: Record<string, HTMLImageElement> = {};

export interface GuiIconCfg extends ShapeAttrs {
  readonly name: string;
}

/**
 *  iconfont  svg  Icon
 */
export class GuiIcon extends Group {
  static type = '__GUI_ICON__';

  // icon  GImage 
  public iconImageShape: Shape.Image;

  constructor(cfg: GuiIconCfg) {
    super(cfg);
    this.render();
  }

  //  Image  new Image() 
  /*  image  */
  public getImage(
    name: string,
    cacheKey: string,
    fill?: string,
  ): Promise<HTMLImageElement> {
    return new Promise<HTMLImageElement>((resolve, reject): void => {
      let svg = getIcon(name);
      if (!svg) {
        return;
      }

      const img = new Image();
      img.onload = () => {
        ImageCache[cacheKey] = img;
        resolve(img);
      };
      img.onerror = reject;

      // 
      // 1base 64
      // 2svg
      // 3
      if (svg.includes(SVG_CONTENT_TYPE) || this.isOnlineLink(svg)) {
        img.src = svg;
        // https://github.com/antvis/S2/issues/2513
        img.crossOrigin = 'anonymous';
      } else {
        //  svg fill
        if (fill) {
          // fill fill
          // svgschema
          // 1fill='' 2fill 3fill-***()
          // eslint-disable-next-line no-useless-escape
          svg = svg.replace(/fill=[\'\"]#?\w+[\'\"]/g, ''); //  fill="red|#fff"
          svg = svg.replace(/fill>/g, '>'); // fill>  >
        }
        svg = svg.replace(
          STYLE_PLACEHOLDER,
          `${STYLE_PLACEHOLDER} fill="${fill}"`,
        );
        //  Firefox: https://github.com/antvis/S2/issues/1571 https://stackoverflow.com/questions/30733607/svg-data-image-not-working-as-a-background-image-in-a-pseudo-element/30733736#30733736
        // https://www.chromestatus.com/features/5656049583390720
        img.src = `${SVG_CONTENT_TYPE};utf-8,${encodeURIComponent(svg)}`;
      }
    });
  }

  /**
   * 1. https://xxx.svg
   * 2. http://xxx.svg
   * 3. //xxx.svg
   */
  public isOnlineLink = (src: string) => {
    return /^(https?:)?(\/\/)/.test(src);
  };

  private render() {
    const { name, fill } = this.cfg;
    const attrs = clone(this.cfg);
    const imageShapeAttrs: ShapeAttrs = {
      ...omit(attrs, 'fill'),
      type: GuiIcon.type,
    };
    const image = new Shape.Image({
      attrs: imageShapeAttrs,
    });

    this.iconImageShape = image;
    this.setImageAttrs({ name, fill });
  }

  public setImageAttrs(attrs: Partial<{ name: string; fill: string }>) {
    let { name, fill } = attrs;
    const { iconImageShape: image } = this;
    //  name  fill 
    name = name || image.attrs.name;
    fill = fill || image.attrs.fill;

    const cacheKey = `${name}-${fill}`;
    const img = ImageCache[cacheKey];

    if (img) {
      // already in cache
      image.attr('img', img);
      this.addShape('image', image);
    } else {
      this.getImage(name, cacheKey, fill)
        .then((value: HTMLImageElement) => {
          //  Cell 
          const canvas = (this.getParent() as S2CellType)?.getMeta?.()
            .spreadsheet?.container;

          // G  refreshElements , ,  https://github.com/antvis/S2/issues/2435
          if (this.destroyed || (canvas && !canvas.get('refreshElements'))) {
            DebuggerUtil.getInstance().logger(`GuiIcon ${name} destroyed.`);
            return;
          }

          image.attr('img', value);
          this.addShape('image', image);
        })
        .catch((event: string | Event) => {
          //  TypeError,  G , 
          if (event instanceof TypeError) {
            // eslint-disable-next-line no-console
            console.warn(`GuiIcon ${name} destroyed:`, event);
            return;
          }
          // eslint-disable-next-line no-console
          console.error(`GuiIcon ${name} load failed:`, event);
        });
    }
  }
}
